#top-navigation {
    display: flex;
    justify-content: space-between;
    align-items: center;

    padding: 0 16px;
    height: 64px;

    min-width: 1888px;
    background-color: rgba(255, 255, 255, 0.5);

    /* text   */
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;

    border: 1px solid rgba(65, 65, 65, 0.2);
}

.top-navigation-left {
    display: flex;
    align-items: center;
    gap: 8px;
}

.top-navigation-logo {
    width: 40px;
    height: 40px;

    background-image: url(../../assets/image/travel-title-icon.png);
    background-size: 100% 100%;

    cursor: pointer;
}

.top-navigation-title {
    font-family: WavvePADO;
    font-size: 24px;
    font-weight: 700;
    color: rgba(7, 65, 115, 1);

    cursor: pointer;
}

.top-navigation-main {
    display: flex;
    align-items: center;
    gap: 20px;
}

.top-navigation-item {
    font-family: WavvePADO;
    font-size: 16px;
    font-weight: 400;
    color: rgba(7, 65, 115, 0.8);

    cursor: pointer;
}

.top-navigation-item:hover {
    color: rgba(7, 65, 115, 1);

    text-shadow: 0px 0px 5px rgba(7, 65, 115, 0.3);
}

.top-navigation-item-active {
    font-family: WavvePADO;
    color: rgba(7, 65, 115, 1);
}

.top-navigation-item-active:hover {
    color: rgba(7, 65, 115, 1);

    text-shadow: 0px 0px 5px rgba(7, 65, 115, 0.3);
}

.loginUser-form {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;

    width: 142px;
    margin: 0 10px;

    cursor: pointer;
}
.user-profile {
    background-size: 50px 50px;
    background-position: center;

    max-width: 50px;
    min-width: 50px;
    height: 50px;

    border-radius: 50%;
}
.top-navigation-right-drop-down {
    display: flex;
    justify-content: center;

    position: relative;
    top: 120%;
    right: 350%;
    z-index: 2;

    width: 200px;
    height: 50px;
    border: 1px solid rgba(7, 65, 115, 0.2);

    background-color: white;

    cursor: default;
}
.navigation-box {
    display: flex;
    align-items: center;
    gap: 10px;
}
.navigation-move-page {
    cursor: pointer;
}
.sign-out-button {
    cursor: pointer;
}
