html {
    scrollbar-width: none;
    -ms-overflow-style: none;
}

html::-webkit-scrollbar {
    display: none;
}

#travelList-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 15px;
    margin: 20px 200px;

    width: 1512px;
}

.travel-top-image {
    display: flex;
    width: 1920px;
    height: 600px;

    background-position: cover;
    background-repeat: no-repeat;
    background-image: url("../../../../assets/image/tour-top-image.jpg");
    background-size: 100% 100%;
    position: absolute;
    z-index: -9999;
    top: 0px;
}

.travel-search-list {
    display: flex;
    flex-direction: column;
    margin: 50px 0;
    background-color: rgba(255, 255, 255, 0.5);

    width: 100%;
    border: 1px solid rgba(7, 65, 115, 0.2);
    border-radius: 8px;

    gap: 20px;
    padding: 30px 10px 10px 10px;

    width: 1288px;
}

.travel-search-location {
    display: flex;
    flex-direction: row;
    gap: 20px;

    align-items: center;
    justify-items: center;

    padding: 0px 0px 0px 32px;

    font-size: 20px;
}

.travel-search-category {
    display: flex;
    flex-direction: row;
    gap: 20px;

    align-items: center;
    justify-items: center;

    padding: 0px 0px 0px 32px;

    font-size: 20px;
}

.travel-font {
    font-size: 20px;
    font-weight: 700;
    color: rgba(7, 65, 115, 1);
}

.travel-search-box {
    display: flex;
    justify-content: space-between;

    padding: 0px 0px 0px 32px;
}

.travel-search-input-box {
    display: flex;
    gap: 20px;
}

.travel-search-input {
    width: 700px;

    outline: none;
    border: 1px solid rgba(7, 65, 115, 0.2);
    border-radius: 8px;

    font-size: 16px;
    font-weight: 700;
}

.travel-list-table {
    display: flex;

    min-width: 1252px;
}

.travel-list-table-th {
    display: flex;
    flex-grow: 1;
    align-items: center;
    padding: 15px 24px;

    border: 1px solid rgba(7, 65, 115, 0.2);
    border-radius: 8px;

    gap: 90px;

    font-size: 16px;
    font-weight: 700;

    color: rgba(64, 64, 64, 1);

    min-width: 1252px;
}

.travel-list-table-show {
    display: flex;
    flex-grow: 1;
    flex-direction: column;
}

.travel-list-table-th:hover {
    cursor: pointer;
    background-color: rgba(238, 238, 238, 0.2);
}

.travel-list-table-title {
    display: flex;

    gap: 20px;

    padding-bottom: 20px;
}

.travel-title {
    align-self: start;
    width: 59px;
    color: lightslategrey;
}

.travel-name {
    display: flex;
    gap: 10px;

    width: 450px;
    text-align: start;
}

.travel-telNumber {
    display: flex;
    gap: 10px;
    flex-grow: 1;

    width: 450px;
    text-align: start;
}

.travel-location {
    display: flex;
    gap: 10px;

    width: 450px;
    text-align: start;
}

.travel-hours {
    display: flex;

    flex-grow: 1;
    gap: 10px;

    width: 500px;
    text-align: start;
}

.travel-list-table-outline {
    display: flex;
    flex-direction: column;

    gap: 10px;
}

.travel-outline-text {
    display: flex;
    flex-direction: column;
    align-items: start;

    gap: 10px;
}

.travel-outline {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    width: 950px;
}

.travel-list-picture {
    width: 200px;
    height: 135px;
}

.travel-list-bottom {
    display: flex;
    justify-content: center;
}

.travel-list-pagenation {
    display: flex;
    align-items: center;

    gap: 24px;
}

.travel-list-page-left {
    width: 40px;
    height: 40px;

    background-image: url(../../../../assets/image/page-left.png);
    background-size: 100% 100%;

    cursor: pointer;
}

.travel-list-page-right {
    width: 40px;
    height: 40px;

    background-image: url(../../../../assets/image/page-right.png);
    background-size: 100% 100%;
    cursor: pointer;
}

.travel-list-page-box {
    display: flex;
    align-items: center;
    gap: 16px;
}

.travel-list-page {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    color: rgba(148, 146, 166, 1);

    cursor: pointer;
}

.travel-list-page-active {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    color: rgba(64, 64, 64, 1);

    cursor: default;
}

.travel-detail-info {
    width: 400px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.ot {
    align-self: start;
}
