#rest-list-table-box {
    display: flex;
    gap: 56px;
    justify-content: center;
    text-align: center;

    width: 100%;

    overflow: hidden;
    white-space: nowrap;

    padding: 10px 0;
    border-bottom: 1px solid rgba(238, 238, 238, 1);
}

.long-text {
    line-height: 52px;
    text-align: left;
}
.control-button {
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;

    padding: 8px 24px;

    background-color: rgba(255, 255, 255, 1);
    border: 1px solid rgba(64, 64, 64, 1);
    border-radius: 6px;

    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 175%;

    color: black;

    cursor: pointer;
}
.controll-button:hover {
    background-color: rgba(238, 238, 238, 1);
}

#rest-list-wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;

    width: 1235px;
}
.rest-list-top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 100%;
}
.rest-list-count-text .emphasis {
    color: rgba(64, 64, 64, 1);
}
.rest-list-control-box {
    display: flex;

    margin-right: 10px;
}
.rest-list-table {
    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 8px;
    height: 750px;

    background-color: rgba(255, 255, 255, 1);
}
.rest-list-table-top {
    display: flex;
    gap: 56px;
    align-items: center;
    justify-content: center;

    width: 100%;
    padding: 15px 0;
    border-bottom: 2px solid rgba(238, 238, 238, 1);

    font-size: 16px;
    font-weight: 700;
    text-align: center;

    color: rgba(64, 64, 64, 1);
}
.rest-list-table-number {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 30px;
}
.rest-list-table-image {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
}
.rest-list-table-name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    width: 128px;

    cursor: pointer;
}
.rest-list-table-locate {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    width: 128px;
}
.rest-list-table-tel {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    width: 128px;
}
.rest-list-table-hours {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    width: 128px;
}
.rest-list-table-button {
    width: 80px;
}
.rest-list-bottom {
    display: flex;
    justify-content: space-between;
}
.rest-list-bottom-left {
    width: 300px;
}
.rest-list-bottom-middle {
    display: flex;
}
.rest-list-pagenation {
    display: flex;
    gap: 8px;
}
.rest-list-page-left {
    width: 40px;
    height: 40px;

    background-image: url("../../../../assets/image/page-left.png");
    background-size: 100% 100%;

    cursor: pointer;
}
.rest-list-page-box {
    display: flex;
    gap: 16px;
    align-items: center;
}
.rest-list-page {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    color: rgba(138, 146, 166, 1);

    cursor: pointer;
}
.rest-list-page-active {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    color: rgba(64, 64, 64, 1);

    cursor: default;
}
.rest-list-page-right {
    width: 40px;
    height: 40px;

    background-image: url("../../../../assets/image/page-right.png");
    background-size: 100% 100%;

    cursor: pointer;
}
.rest-list-bottom-right {
    width: 300px;
}
.rest-list-search-box {
    display: flex;
    align-items: center;
    gap: 12px;
}
.rest-list-search-input-box {
    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 6px;
    padding: 8px 16px;

    width: 168px;

    background-color: rgba(255, 255, 255, 1);
}
.rest-list-search-input {
    border: none;
    outline: none;
    background: none;
    width: 100%;

    font-size: 16px;
    font-weight: 400;
    line-height: 175%;
}
