.user-list-table-box {
    display: flex;
    justify-content: space-around;
    text-align: center;

    width: 100%;

    padding: 10px 0;
    border-bottom: 1px solid rgba(238, 238, 238, 1);
}
.user-list-table-number {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 200px;
}
.user-list-table-id {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
}
.user-list-table-nickname {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
}
.user-list-table-email {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
}
.user-list-table-delete {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100px;
}

#user-list-wrapper {
    display: flex;
    flex-direction: column;
    gap: 10px;

    width: 1235px;
}

.user-list-top {
    display: flex;
    align-items: center;
    height: 44px;
}
.user-list-count-text .emphasis {
    color: rgba(64, 64, 64, 1);
}

.user-list-table {
    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 8px;
    height: 750px;

    background-color: rgba(255, 255, 255, 1);
}
.user-list-table-top {
    display: flex;
    justify-content: space-around;
    align-items: center;

    width: 100%;
    padding: 15px 0;
    border-bottom: 1px solid rgba(238, 238, 238, 1);

    font-size: 16px;
    font-weight: 700;
    text-align: center;

    color: rgba(64, 64, 64, 1);
}

.user-list-bottom {
    display: flex;
    justify-content: space-between;
}
.user-list-bottom-left {
    width: 300px;
}
.user-list-bottom-middle {
    display: flex;
}
.user-list-pagenation {
    display: flex;
    gap: 8px;
}
.user-list-page-left {
    width: 40px;
    height: 40px;

    background-image: url("../../../../assets/image/page-left.png");
    background-size: 100% 100%;

    cursor: pointer;
}
.user-list-page-box {
    display: flex;
    gap: 16px;
    align-items: center;
}

.user-list-page {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    color: rgba(138, 146, 166, 1);

    cursor: pointer;
}
.user-list-page-active {
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;

    color: rgba(64, 64, 64, 1);

    cursor: default;
}
.user-list-page-right {
    width: 40px;
    height: 40px;

    background-image: url("../../../../assets/image/page-right.png");
    background-size: 100% 100%;

    cursor: pointer;
}
.user-list-bottom-right {
    width: 300px;
}

.user-list-search-box {
    display: flex;
    align-items: center;
    gap: 12px;
}
.user-list-search-input-box {
    border: 1px solid rgba(238, 238, 238, 1);
    border-radius: 6px;
    padding: 8px 16px;

    width: 168px;

    background-color: rgba(255, 255, 255, 1);
}
.user-list-search-input {
    border: none;
    outline: none;
    background: none;
    width: 100%;

    font-size: 16px;
    font-weight: 400;
    line-height: 175%;
}
